import React from 'react';
import LoadIndicator from '../components/LoadIndicator';
import { getChannels } from '../db';
import './Start.css';

function saveDefaultChannels() {
  const channels = [
    {
      title: 'CNN World',
      address: 'http://rss.cnn.com/rss/edition_world.rss',
      active: true,
      category: 'news'
    },
    {
      title: 'ELLE',
      address: 'https://www.elle.com/rss/all.xml/',
      active: true,
      category: 'fashion'
    },
    {
      title: 'WIRED',
      address: 'https://www.wired.com/feed/rss',
      active: true,
      category: 'technology'
    }
  ];

  window.localStorage.setItem('channels', JSON.stringify(channels));
}

function StartPage(props) {
  let index = 0;
  const views = [
    <LoadIndicator />,
    <div style={{ flex: 1, textAlign: 'center' }}>
      <button onClick={() => {
        saveDefaultChannels();
        props.onClicked();
      }}>Continue</button>
    </div>
  ];

  let channels = getChannels();
  if (channels.length === 0) {
    index = 1;
  } else {
    props.onClicked();
  }

  return (
    <div className='page StartPage'>
      <div style={{ flex: index + 1 }}>
        <h1>Welcome to <span>FeedGram</span></h1>
        <p>Get latest news and feeds from sources you love for free!</p>
      </div>

      { views[index] }

      <div className='container' style={{ textAlign: 'center' }}>
        <p>v0.1.0</p>
        <p>Built with love in India.</p>
      </div>
    </div>
  );
}

export default StartPage;
