import React, { useState } from 'react';
import Channel from '../components/Channel';
import FabAddButton from '../components/FabAddButton';
import { getChannels, removeChannel, toggleChannel, saveChannel } from '../db';
import './ChannelList.css';

function ChannelListPage() {
  const [channels, setChannels] = useState(getChannels());

  function toggle(item) {
    setChannels(toggleChannel(item));
  }

  function remove(item) {
    setChannels(removeChannel(item));
  }

  function save(item) {
    setChannels(saveChannel(item));
  }

  return (
    <div className='ChannelListPage'>
      { channels.map((c, i) => <Channel key={i} item={c} toggleChannel={toggle} removeChannel={remove} />) }

      <FabAddButton saveChannel={save} />
    </div>
  );
}

export default ChannelListPage;
