function getChannels() {
  let channels = window.localStorage.getItem('channels');
  if (!channels)
    channels = '[]';
  channels = JSON.parse(channels);
  return channels;
}

function saveChannel(channel) {
  let channels = getChannels();
  channels.push(channel);
  saveChannels(channels);
  return channels;
}

function saveChannels(channels) {
  window.localStorage.setItem('channels', JSON.stringify(channels));
  makeChannelsDirty();
}

function removeChannel(item) {
  let channels = getChannels();
  channels.splice(channels.indexOf(item), 1);
  saveChannels(channels);
  return channels;
}

function toggleChannel(item) {
  let channels = getChannels();
  let index = 0;
  for (; index < channels.length; index++) {
    if (channels[index].address === item.address)
      break;
  }

  channels[index].active = !item.active;
  saveChannels(channels);
  return channels;
}

function makeChannelsDirty() {
  window.localStorage.setItem('lastFetched', '0');
}

export { getChannels, removeChannel, toggleChannel, saveChannel };
