import React, { Component } from 'react';
import './Home.css';
import { getChannels } from '../db';
import parseFeed from '../parser';
import Card from '../components/Card';
import LoadIndicator from '../components/LoadIndicator';

const CLASS_NAME = 'HomePage';


function getCorsAddress(address) {
  if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
    return `/${address}`;
  }

  return `https://cors-anywhere.herokuapp.com/${address}`;
}


class HomePage extends Component {
  constructor(props) {
    super(props);

    this.state = { feeds: [], feedsToLoad: 0 };

    this.onFeedLoaded = this.onFeedLoaded.bind(this);
  }

  setStateSafe(state) {
    if (!this.mounted)
      return;
    this.setState(state);
  }

  setStateAsync(state) {
    return new Promise(resolve => this.setStateSafe(state, resolve));
  }

  componentDidMount() {
    this.mounted = true;

    let lastFetched = window.localStorage.getItem('lastFetched', Date.now());
    if (!lastFetched || Date.now() - parseInt(lastFetched) > 60000) {
      this.fetchFeeds();
    } else {
      let data = window.localStorage.getItem('feeds');
      let feeds = JSON.parse(data);
      this.setStateSafe({feeds: feeds, feedsToLoad: 0});
    }
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  async fetchFeeds() {
    let channels = getChannels();
    await this.setStateAsync({ feedsToLoad: channels.length });

    for (let i = 0; i < channels.length; i++) {
      const channel = channels[i];
      if (!channel.active) {
        this.onFeedLoaded();
        continue;
      }

      const address = getCorsAddress(channel.address);
      console.log(address);
      parseFeed(address)
        .then(r => this.setStateSafe({ feeds: this.state.feeds.concat(r) }))
        .catch(e => { console.log(e) })
        .finally(this.onFeedLoaded);
    }
  }

  onFeedLoaded() {
    this.setStateSafe({ feedsToLoad: this.state.feedsToLoad - 1 });
    if (this.state.feedsToLoad === 0)
      this.saveFeeds();
  }

  saveFeeds() {
    let feeds = JSON.stringify(this.state.feeds);
    window.localStorage.setItem('feeds', feeds);
    window.localStorage.setItem('lastFetched', Date.now());
  }

  render() {
    let cards = [];
    if (this.state.feedsToLoad === 0) {
      let items = [];
      for (let i = 0; i < this.state.feeds.length; i++) {
        let feed = this.state.feeds[i];
        for (let j = 0; j < feed.items.length; j++) {
          let item = feed.items[j];
          item.channel = {};
          item.channel.title = feed.title;
          item.channel.link = feed.link;

          let faviconURL = new URL(feed.link);
          item.channel.icon = `https://f1.allesedv.com/32/${faviconURL.hostname}`
          items.push(item);
        }
      }

      items.sort((a, b) => a.isoDate > b.isoDate ? -1 : 1);

      for (let i = 0; i < items.length; i++) {
        cards.push(<Card key={i} item={items[i]} />);
      }
    }

    return (
      <div className={CLASS_NAME}>
        { this.state.feedsToLoad === 0 ? cards : <LoadIndicator /> }
      </div>
    );
  }
}

export default HomePage;
