import Parser from 'rss-parser';


function trimContent(str) {
  if (!str) return str;

  str = str.split(/^(.*?)[.?!]\s/)[0];
  if (str.length > 200) {
    str = str.substring(0, 200) + '...';
  }

  return str;
}


async function parseFeed(address) {
  let parser = new Parser();
  let feed = await parser.parseURL(address);
  delete feed.copyright;
  delete feed.description;
  delete feed.generator;
  delete feed.language;
  for (let i = 0; i < feed.items.length; i++) {
    delete feed.items[i].content;
    delete feed.items[i].guid;
    feed.items[i].contentSnippet = trimContent(feed.items[i].contentSnippet);
  }
  return feed;
}

export default parseFeed;
