import React, { useState } from 'react';
import './TabView.css';

import HomePage from './pages/Home';
import ChannelListPage from './pages/ChannelList';
import SettingsPage from './pages/Settings';

import { IconContext } from 'react-icons';
import { AiOutlineHome, AiOutlineUnorderedList, AiOutlineSetting } from 'react-icons/ai';

function TabView() {
  const [index, setIndex] = useState(0);
  const views = [<HomePage />, <ChannelListPage />, <SettingsPage />];

  return (
    <div className='TabView'>

      { views[index] }

      <div className='BottomAppBar'>
        <IconContext.Provider value={{ size: '1.5em' }}>
          <div onClick={() => setIndex(0)} style={{ color: index === 0 ? 'var(--primary-color)' : 'inherit' }}><AiOutlineHome /></div>
          <div onClick={() => setIndex(1)} style={{ color: index === 1 ? 'var(--primary-color)' : 'inherit' }}><AiOutlineUnorderedList /></div>
          <div onClick={() => setIndex(2)} style={{ color: index === 2 ? 'var(--primary-color)' : 'inherit' }}><AiOutlineSetting /></div>
        </IconContext.Provider>
      </div>
    </div>
  );
}

export default TabView;
