import React, { useState } from 'react';
import './Channel.css';
import { IconContext } from 'react-icons';
import { AiOutlineEye, AiOutlineMinusCircle } from 'react-icons/ai';

function Channel(props) {
  const [active, setActive] = useState(props.item.active);

  return (
    <div className='Channel'>
      <div className='ChannelMetadata'>
        <h3>{ props.item.title }</h3>
        <div>{ props.item.address }</div>
      </div>
      <div className='ChannelAction'>
        <IconContext.Provider value={{ size: '1.5em' }}>
          <AiOutlineEye
            onClick={() => {
              setActive(!active);
              props.toggleChannel(props.item);
            }}
            style={{ marginRight: 18, color: active ? 'var(--primary-color)' : 'var(--light-text-color)' }}
          />
          <AiOutlineMinusCircle style={{ color: 'red' }} onClick={() => props.removeChannel(props.item) }/>
        </IconContext.Provider>
      </div>
    </div>
  );
}

export default Channel;
