import React from 'react';
import './Settings.css';

function SettingsPage() {
  return (
    <div className='SettingsPage'>
      <div>About FeedGram</div>
      <div>Contact</div>
      <div>Terms of Use</div>
      <div>Open Source Licenses</div>
      <div onClick={() => {
        window.localStorage.setItem('lastFetched', '0');
        window.localStorage.setItem('feeds', '[]');
        window.location.reload();
      }}>Empty Feeds Cache</div>
      <div onClick={() => {
        window.localStorage.clear();
        window.location.reload();
      }} style={{ color: 'red' }}>Clear All Data</div>
    </div>
  );
}

export default SettingsPage;
