import React, { useState } from 'react';
import './App.css';

import TabView from './TabView';
import StartPage from './pages/Start';

function App() {
  const [index, setIndex] = useState(0);
  const views = [<StartPage onClicked={() => setIndex(1)} />, <TabView />]
  return (
    views[index]
  );
}

export default App;
