import React, { useState } from 'react';
import './FabAddButton.css';

import { IconContext } from 'react-icons';
import { AiOutlinePlus } from 'react-icons/ai'

function ChannelModal(props) {
  const [title, setTitle] = useState('');
  const [address, setAddress] = useState('');

  return (
    <div className='ChannelModalOuter' onClick={() => props.onReject()}>
      <div className='ChannelModal' onClick={ e => e.stopPropagation() }>
        <h3>Add New Channel</h3>
        <input type='text' placeholder='Title' value={title} onChange={e => setTitle(e.target.value)}/>
        <input type='text' placeholder='Feed Address' value={address} onChange={e => setAddress(e.target.value)}/>
        <button onClick={() => {
          if (!title.trim() || !address.trim()) return;

          let channel = {};
          channel.title = title;
          channel.address = address;
          channel.active = true;
          props.saveChannel(channel);
          props.onReject();
        }}>Save</button>
      </div>
    </div>
  );
}

function FabAddButton(props) {
  const [visible, setVisible] = useState(false);
  return (
    <>
      { visible ? <ChannelModal saveChannel={props.saveChannel} onReject={() => setVisible(false)}/> : <></> }

      <div className='FabAddButton' onClick={() => setVisible(true) }>
        <IconContext.Provider value={{ size: '2em' }}>
          <AiOutlinePlus style={{ color: 'white' }} />
        </IconContext.Provider>
      </div>
    </>
  );
}

export default FabAddButton;
