import React from 'react';
import moment from 'moment';
import './Card.css';


function Card(props) {
  return (
    <div className='Card'>
      <div className='CardHeading'>
        <div className='SourceIcon' style={{ backgroundImage: `url("${props.item.channel.icon}")` }}></div>
        <div className='SourceMetadata'>
          <div className='SourceName'>{ props.item.author || props.item.creator || props.item.channel.title }</div>
          <div className='SourcePubDate'>{ moment(props.item.pubDate).fromNow() }</div>
        </div>
      </div>
      <div className='CardContent'>
        <h3>{ props.item.title }</h3>
        <div>{ props.item.contentSnippet }</div>
        <div>
          <a href={props.item.link} target='_blank' rel='noopener noreferrer'>Read More</a>
        </div>
      </div>
    </div>
  );
}

export default Card;
